}

body {
  background: #f0f0f0;
}

@media all and (min-width: 49rem) {
  .topbar .pane-search-form {
    order: 3;
  }
}

@media all and (min-width: 49rem) {
  .topbar #header {
    order: 1;
  }
}

.site-header {
  background: white;
}

.pane-menu-block-main-menu {
  width: 100%;
}

@media all and (min-width: 49rem) {
  .pane-menu-block-main-menu {
    width: auto;
    order: 2;
  }
}

.topbar-inner {
  display: flex;
  flex-wrap: wrap;
  height: 11.9rem;
}

@media all and (min-width: 49rem) {
  .topbar-inner {
    flex-wrap: nowrap;
    justify-content: center;
    height: 5rem;
  }
}

.js .js-login-form {
  display: none;
}

.menu-name-main-menu {
  height: 5rem;
  background-color: #b81b40;
}

.menu-name-main-menu ul {
  display: flex;
  justify-content: space-between;
  height: 100%;
  margin: 0 5vw;
  list-style-type: none;
}

.menu-name-main-menu li {
  display: flex;
  align-content: center;
  align-items: center;
}

.menu-name-main-menu li a {
  color: white;
  text-decoration: none;
  font-family: "SourceSansPro", sans-serif;
  font-weight: bold;
  font-size: 1rem;
}

@media all and (min-width: 49rem) {
  .menu-name-main-menu li a {
    height: 100%;
    padding: 2.5rem;
    color: #505050;
    line-height: 5rem;
    font-weight: 700;
  }
  .menu-name-main-menu li a:hover {
    background: #f0f0f0;
  }
}

@media all and (min-width: 49rem) {
  .menu-name-main-menu {
    background-color: transparent;
  }
}

