.footer {
  background-color: #282828;
  color: white;
  font-size: 100%;
  font-family: "SourceSansPro", sans-serif;
  overflow: hidden;
  width: 100%;
  float: left;
  display: inline-block;
}

.footer-inner {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

@media all and (min-width: 49rem) {
  .footer-inner {
    display: flex;
    padding: 4rem 0;
  }
}

.footer .menu a {
  text-transform: none;
}

.footer a:hover {
  text-decoration: underline;
}

.footer ul {
  display: none;
  margin: 0;
  padding: 0;
}

@media all and (min-width: 49rem) {
  .footer ul {
    display: block;
  }
}

.footer ul li {
  list-style: none;
  padding: 0;
  margin: 0;
}

@media all and (min-width: 49rem) {
  .footer ul li {
    display: block !important;
  }
}

.footer .panel-pane a {
  display: block;
  color: #787878;
  font-size: 1.1rem;
  text-decoration: none;
  padding: 1rem 2rem;
}

@media all and (min-width: 49rem) {
  .footer .panel-pane {
    flex: 1;
  }
}

.footer .pane-title {
  color: white;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  cursor: pointer;
  text-transform: uppercase;
  font-size: 1.1rem;
  margin: 0;
  padding: 2rem;
}

@media all and (min-width: 49rem) {
  .footer .pane-title {
    border-top: none;
  }
}

.footer .leaf {
  margin: 0;
}
